import Vue from 'vue'
import vuex from 'vuex'
import vueResource from 'vue-resource';
Vue.use(vuex)
Vue.use(vueResource)
const store = new vuex.Store({
  state: {
    nbaData:{},
    nbaDataList:{}
  },
  mutations: {
    haveDatas( state ,val ){
      state.nbaData = val
    },
    haveDatasList( state,val ){
      state.nbaDataList = val
    }
  },
  actions: {
    getDatas( store , val ){
      Vue.http({
        url: 'http://op.juhe.cn/onebox/basketball/nba',
        params: {
          key: 'a8abfa1e921d5f46fac3eff086a30e0a',

        },

        method: 'jsonp'
      }).then(response => {
        store.commit('haveDatas' , response.body.result );
        store.commit('haveDatasList',response.body.result.list[0]['tr'])
      }, error => {
        console.log(error);

      })
    }
  },
  getters: {}
})

export default store



